import styled from 'styled-components';

export const Box = styled.div`
    ${props => props.minHeight?`min-height: ${props.minHeight};`:''}
    ${props => props.borderBottom?`border-bottom: 2px solid ${props.theme.getColor(props.theme, props.borderBottom, props.theme.neutralL)};`:''}
    ${props => props.borderTop?`border-top: 2px solid ${props.theme.getColor(props.theme, props.borderTop, props.theme.neutralL)};`:''}
    background-color: ${props => props.backgroundColor || 'transparent'};
    max-width: ${props => props.maxWidth?props.theme[props.maxWidth]:props.maxWidth || props.theme.mediaM};
    margin: auto;
    
`